@import url(https://fonts.googleapis.com/css?family=Abril+Fatface" rel="stylesheet">);
.createbox {
    display: flex;
    justify-content: space-between;
    width: 24em;
}

.createbutton {
    margin-top: 2em;
}
.familyaccount {
    margin-bottom: 3em;
    margin-top: 3em;
    width: 50em;
    flex-wrap: wrap;
}

.accountbox {
    display: flex;
    justify-content: space-between;
}

.accountlink {
    padding-top: 3.5em;
    color: #67C2D0;
}

.subtitle {
    margin-top: .5em;
}

.header {
    display: flex;
    justify-content: center;
}

.header img {
    margin-left: 1em;
    width: 10em;
    height: 15em;
}

.title {
    margin-left: 2em;
    align-self: center;
}

.header h1 {
    font-size: 2em;
    font-family: 'Abril Fatface';
}
.homepage {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    margin-top: 2em;
}

.createaccounts {
    margin-top: 4em;
    margin-bottom: 2.5em;
}
.control {
    margin-top: 2em;
    margin-bottom: 2em;
    display: flex;
    flex-direction: column;
}

.submitbutton {
    margin-top: 2em;
    align-self: flex-end;
}

.forminput1 {
    margin-bottom: 1em;
    width: 20em;
}


.viewtask {
    display: flex;
    justify-content: center;
}

.viewbox {
    margin-top: 4em;
}

.viewtitle {
    margin-top: 2em;
    margin-bottom: 2em;
    font-weight: bolder;
}

.viewsubtitle {
    font-weight: bold;
    margin-bottom: 2em;
}



